.modal--hidden {
  position: fixed; /*   */
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0,0,0,0.5); /*   */
  z-index: 1050;
  opacity: 0; /*      */
  visibility: hidden;
  -webkit-transition: opacity 200ms ease-in;
  -moz-transition: opacity 200ms ease-in;
  transition: opacity 200ms ease-in; /*   */
  pointer-events: none; /*      */
  margin: 0;
  padding: 0;
}
/*     */
.modal--active {
  display: flex;
  align-items: center; /*      */
  opacity: 1; /*    */
  visibility: visible;
  pointer-events: auto; /*      */
  overflow-y: auto; /*    y,       */
}
/*         */
.modal__dialog {
  position: relative;
  width: auto;
  margin: 10px;
}
@media (min-width: 576px) {
  .modal__dialog {
    max-width: 500px;
    margin: 30px auto; /*       */
  }
}
/*   ,     */
.modal__content {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0,0,0,.2);
  border-radius: .3rem;
  outline: 0;
}
@media (min-width: 768px) {
  .modal__content {
    box-shadow: 0 5px 15px rgba(0,0,0,.5);
  }
}
/*      */
.modal__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  border-bottom: 1px solid #eceeef;
}
.modal__title {
  font-family: 'Roboto', sans-serif;
  margin-top: 0;
  margin-bottom: 0;
  line-height: 1.5;
  font-size: 1.25rem;
  font-weight: 500;
  color: #413d4b;
}

.modal__text {
  color: #413d4b;
}

/*    "" */
.modal__close {
  float: right;
  font-family: sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 1;
  color: #000;
  text-shadow: 0 1px 0 #fff;
  opacity: .5;
  text-decoration: none;
}
/*    ""        */
.modal__close:focus, .modal__close:hover {
  color: #000;
  text-decoration: none;
  cursor: pointer;
  opacity: .75;
}
/*   ,     */
.modal__body {
  position: relative;
  -webkit-box-flex: 1;
  -webkit-flex: 1 1 auto;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  padding: 15px;
  overflow: auto;
}